.sb-table-head {
  --sb-table-row-border-bottom: 2px solid var(--sbt-theme-tertiary-color-50);
  --sb-table-header-cell-vertical-align: bottom;
  --sb-table-header-cell-text-align: left;
}

.sb-table-head_sticky {
  --sb-table-header-cell-position: sticky;
  --sb-table-header-cell-top: -1px;
  --sb-table-header-cell-z-index: var(--sbt-stack-floating);
  --sb-table-header-cell-color-background: var(--sbt-white);
}

.sb-table-head_fill {
  --sb-table-header-color-background: var(--sbt-theme-tertiary-color-10);
  --sb-table-row-border-bottom: none;
  --sb-table-row-border-radius: 0.25rem;

  .sb-table-row {
    background-color: var(--sb-table-header-color-background);
  }
}
