:root {
  --sb-table-color-border: var(--sbt-theme-tertiary-color-10);
  --sb-table-color-text: var(--sbt-theme-tertiary-color-80);
  --sb-table-header-color-background: var(--sbt-theme-tertiary-color-20);
  --sb-table-header-active-color-background: var(--sbt-theme-color-30);
  --sb-table-striped-row-even-color-background: var(
    --sbt-theme-tertiary-color-10
  );
  --sb-table-header-cell-font-size: var(--sbt-font-size-20);
  --sb-table-font-size: var(--sbt-font-size-20);
}

.sb-table {
  color: var(--sb-table-color-text);
  display: table;
  flex-grow: 1;
  font-size: var(--sb-table-font-size);
  line-height: var(--sbt-line-height-ui);
  width: 100%;
}

.sb-table-small {
  --sb-table-cell-padding: 0.3rem;
  --sb-table-header-cell-padding: 0.3rem;
  --sb-table-cell-font-size: var(--sbt-font-size-25);
  --sb-table-header-cell-font-size: var(--sbt-font-size-20);
}

.sb-table-large {
  --sb-table-cell-font-size: var(--sbt-font-size-30);
  --sb-table-header-cell-font-size: var(--sbt-font-size-20);
  line-height: var(--sbt-line-height-75);
}

// TODO: ISSUE-270 Flatten selector
.sb-pinned-first-column {
  .sb-table-header-cell:first-child,
  .sb-table-cell:first-child {
    left: -1px;
    position: sticky;
    z-index: var(--sbt-stack-floating);
  }

  .sb-table-header-cell:first-child {
    .sb-table-header-cell-text {
      &::after {
        content: '\f08d';
        font-family: 'Font Awesome 6 Pro';
        font-size: var(--sbt-font-size-20);
        padding-left: 5px;
      }
    }
  }

  .sb-table-cell:first-child {
    --sb-table-cell-background-color: var(--sbt-color-background);
    box-shadow: 2px 0 5px -2px var(--sbt-theme-tertiary-color-60);
  }
}

// TODO: ISSUE-270 Flatten selector
.sb-pinned-last-column {
  .sb-table-header-cell:last-child,
  .sb-table-cell:last-child {
    position: sticky;
    right: -1px;
    z-index: var(--sbt-stack-floating);
  }

  .sb-table-header-cell:last-child {
    .sb-table-header-cell-text {
      &::after {
        content: '\f08d';
        font-family: 'Font Awesome 6 Pro';
        font-size: var(--sbt-font-size-20);
        padding-left: 5px;
      }
    }
  }

  .sb-table-cell:last-child {
    --sb-table-cell-background-color: var(--sbt-color-background);
    box-shadow: -2px 0 5px -2px var(--sbt-theme-tertiary-color-60);
  }
}

.sb-table-bordered {
  --sb-table-cell-border-vertical: 1px solid var(--sb-table-color-border);
  --sb-table-cell-border-horizontal: 1px solid var(--sb-table-color-border);
  --sb-table-header-cell-border-vertical: 1px solid var(--sb-table-color-border);
  --sb-table-header-cell-border-horizontal: 1px solid
    var(--sb-table-color-border);
  border: 1px solid var(--sb-table-color-border);
}

.sb-table-bordered-vertical {
  --sb-table-header-cell-border-vertical: 1px solid var(--sb-table-color-border);
  --sb-table-cell-border-vertical: 1px solid var(--sb-table-color-border);
}

.sb-table-bordered-horizontal {
  --sb-table-header-cell-border-horizontal: 1px solid
    var(--sb-table-color-border);
  --sb-table-cell-border-horizontal: 1px solid var(--sb-table-color-border);
}

.sb-table-bordered-rows {
  --sb-table-border-rows: 1px solid var(--sb-table-color-border);
}

.sb-table-borderless {
  --sb-table-header-cell-border: 0;

  .sb-table-body + .sb-table-body {
    border: 0;
  }
}

// TODO: ISSUE-270 Flatten selector
.sb-table-striped .sb-table-body .sb-table-row:nth-of-type(even) {
  background-color: var(--sb-table-striped-row-even-color-background);
}
// TODO: ISSUE-270 Flatten selector
.sb-table-hover .sb-table-body .sb-table-row:hover {
  background-color: var(--sbt-theme-color-10);
  color: var(--sb-table-row-hover-color-text);
}

@media (max-width: 575.98px) {
  .sb-table-responsive-sm {
    display: block;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    width: 100%;
  }
  // TODO: ISSUE-270 Flatten selector
  .sb-table-responsive-sm > .sb-table-bordered {
    border: 0;
  }

  .sb-table {
    &.sb-table-sm-lg {
      font-size: 100%;
      padding: 0.5rem;
    }
  }
}

@media (max-width: 767.98px) {
  .sb-table-responsive-md {
    display: block;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    width: 100%;
  }
  // TODO: ISSUE-270 Flatten selector
  .sb-table-responsive-md > .sb-table-bordered {
    border: 0;
  }
}

@media (max-width: 991.98px) {
  .sb-table-responsive-lg {
    display: block;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    width: 100%;
  }
  // TODO: ISSUE-270 Flatten selector
  .sb-table-responsive-lg > .sb-table-bordered {
    border: 0;
  }
}

@media (max-width: 1199.98px) {
  .sb-table-responsive-xl {
    display: block;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    width: 100%;
  }
  // TODO: ISSUE-270 Flatten selector
  .sb-table-responsive-xl > .sb-table-bordered {
    border: 0;
  }
}

.sb-table-responsive {
  display: block;
  -webkit-overflow-scrolling: touch;
  overflow-x: auto;
  position: relative;
  width: 100%;
  @media not all and (min-resolution: 0.001dpcm) {
    will-change: transform;
  }
}

.sb-table-row-space-normal,
.sb-table-row-space-tight,
.sb-table-row-space-loose {
  border-collapse: separate;

  &.sb-table-bordered {
    --sb-table-border-rows: 1px solid var(--sb-table-color-border);
    border: 0;

    .sb-table-header-cell {
      border-top: 1px solid var(--sb-table-color-border);
    }
  }
}

.sb-table-row-space-normal {
  border-spacing: 0 var(--sbt-spacing-20);
}

.sb-table-row-space-tight {
  border-spacing: 0 var(--sbt-spacing-10);
}

.sb-table-row-space-loose {
  border-spacing: 0 var(--sbt-spacing-40);
}
