.sb-table-cell {
  background-color: var(--sb-table-cell-background-color);
  border-bottom: var(
    --sb-table-cell-border-horizontal,
    var(--sb-table-border-rows)
  );
  border-right: var(--sb-table-cell-border-vertical);
  border-top: var(--sb-table-border-rows);
  font-size: var(--sb-table-cell-font-size);
  padding: var(--sb-table-cell-padding, 0.75rem 0.5rem);
  vertical-align: top;
  white-space: nowrap;

  &:first-child {
    border-left: var(
      --sb-table-cell-border-vertical,
      var(--sb-table-border-rows)
    );
  }

  &:last-child {
    border-right: var(--sb-table-border-rows);
  }
}

.sb-table-cell-wrap {
  white-space: normal;
}
