.sb-table-collapsible {
  width: 28px;
}

.sb-table-collapsible-icon {
  --sb-button-primary-color: var(--sbt-theme-color-100);
  margin: 0;
  padding: 0;
}

.sb-table-collapsible-icon:focus {
  box-shadow: none;
}

.sb-table-collapsible-icon:disabled {
  background-color: transparent;
}

.sb-table-row {
  border-bottom: var(--sb-table-row-border-bottom);
  border-radius: var(--sb-table-row-border-radius);

  th:first-child,
  td:first-child {
    border-bottom-left-radius: var(--sb-table-row-border-radius);
    border-top-left-radius: var(--sb-table-row-border-radius);
  }

  th:last-child,
  td:last-child {
    border-bottom-right-radius: var(--sb-table-row-border-radius);
    border-top-right-radius: var(--sb-table-row-border-radius);
  }
}

.sb-table-row-expanded {
  border-bottom: 2px solid var(--sbt-theme-tertiary-color-30);
}

.sb-table-row-selected {
  background-color: var(--sbt-theme-color-10);
  color: var(--sb-table-row-hover-color-text);
}
