.sb-table-header-cell {
  background-color: var(
    --sb-table-header-cell-color-background,
    var(--sbt-color-background),
    none
  );
  border: var(--sb-table-header-cell-border);
  border-bottom: var(--sb-table-header-cell-border-horizontal);
  border-right: var(--sb-table-header-cell-border-vertical);
  bottom: var(--sb-table-header-cell-bottom);
  color: var(--sb-table-header-color, var(--sbt-theme-color-90));
  font-size: var(--sb-table-header-cell-font-size);
  font-weight: var(
    --sb-table-header-cell-font-weight,
    var(--sbt-font-weight-70)
  );
  left: var(--sb-table-header-cell-left);
  padding: var(--sb-table-header-cell-padding, 0.5rem);
  position: var(--sb-table-header-cell-position);
  right: var(--sb-table-header-cell-right);
  text-align: var(--sb-table-header-cell-text-align);
  top: var(--sb-table-header-cell-top);
  vertical-align: var(--sb-table-header-cell-vertical-align, top);
  white-space: nowrap;
  width: var(--sb-table-header-cell-width);
  z-index: var(--sb-table-header-cell-z-index);

  &:first-child {
    border-left: var(--sb-table-header-cell-border-vertical);
  }
}

.sb-table-header-cell-text {
  border-radius: 5px;
  display: inline-block;
  padding: 5px 0;
}

.sb-table-header-cell-checkbox {
  --sb-table-header-cell-width: 1rem;
}

.sb-table-header-cell_sortable {
  cursor: pointer;
  margin-right: 1.25rem;
  padding: 5px 30px 5px 5px;
  position: relative;
  transition: all 0.2s ease-in-out;

  &:hover {
    background-color: var(--sbt-theme-color-10);
    border-radius: 5px;
  }

  &::after {
    content: '\f0dc';
    font-family: 'Font Awesome 6 Pro';
    font-size: var(--sbt-font-size-25);
    padding-left: 10px;
    position: absolute;
  }
}

.sb-table-header-cell_active {
  background-color: var(--sbt-theme-tertiary-color-10);
}

.sb-table-header-cell_sorted-asc {
  &::after {
    content: '\f161';
  }
}

.sb-table-header-cell_sorted-desc {
  &::after {
    content: '\f160';
  }
}

.sb-table-header-cell_resizable {
  overflow: auto;
  resize: horizontal;
}
